@import url('https://fonts.googleapis.com/css?family=Coiny');
@import url('https://fonts.googleapis.com/css2?family=Mr+Dafoe&display=swap');

.container { 
  display: flex;
  justify-content: center;
  align-items: center;   
   
}

.greeting {
  text-align: center;
  letter-spacing: 0;
}

.hello {
  font-size: 5em;  
  font-family: 'Coiny';
  background: linear-gradient(
    110deg,
    rgb(40, 96, 148) 0%,
    rgb(150, 5, 82) 100%
  );
  display: inline; 
  border-radius: 5px;  
  color: transparent;
  -webkit-background-clip: text;
}

.myname {  
  font-size: 8em;
  transform: rotate(-9deg);
  color: rgb(241, 231, 231);
  font-family: 'Mr Dafoe';
  text-shadow: 0 0 0.05em #fff, 0 0 0.2em #190f18, 0 0 0.3em #fe05e1;
  margin-top: -0.9em;
}
 
@media (max-width: 768px) {
  .hello {
    font-size: 2em;  
  }

  .myname {
    font-size: 2.5em;  
  }

  .container {
    flex-direction: column;
    align-items: center; 
  }
}
