.paper-square-background {
    background-color: #f9f9f9;
    border: 1px solid #e0e0e0;
    max-width: 70em;
    margin: 0 auto;
    display: flex;
    margin: 50px auto 0 auto;  
    justify-content: center;
    align-items: center;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
} 
  
#experience {
    margin-top: -50px;
    display: flex;
    flex-direction: column; 
    font-family: Arial, sans-serif;
    color: black;
  } 
  
  .aboutme-icons {
    display: flex;
    justify-content: center;
    margin: 20px 0;  

  }
  
  .aboutme-icons button {
    margin: 10px;
  } 

.my-experience{
    padding: 20px; 
}